<style src="@vueform/slider/themes/default.css"></style>
<style>
@import './betri-bank-style.css';
.neos-grid-container {
  width: 1120px;
  max-width: calc(100% - 2.8rem);
  margin: 2.8rem auto 8rem;
}
@import './app.css';
</style>

<template>

  <div class="neos-grid-container">
    <div class="betri-calc-title">
      Royn Orkuroknaran
    </div>
    <p>
      Niðanfyri kanst tú kanna, hvussu nógv tú kanst spara við at skifta til varandi orkuskipan.
    </p>

    <!-- input -->
    <div class="betri-grid-column">
      <div class="betri-card">
        <div class="neos-col-lion-100">
          <div class="burdardygd-text-bold" style="margin-bottom: 10px;">
            Tú ynskir
          </div>
          <div class="betri-heating-types">
            <div v-bind:key="item" v-for="item in renovationTypes" class="betri-input-radio"><label
                class="betri-input-label"><input type="radio" :value=item v-model="renovation.RenovationType"
                  @change="onRenovationTypeChanged">{{item}}</label></div>
          </div>
        </div>

        <!-- Jarðhiti og Hitapumpa -->
        <div class="neos-col--lion-100" v-show="isJardhiti || isHitapumpa">

          
          <div class="clearfix spacer-20">
            <div class="burdardygd-text-bold" style="margin-bottom: 10px;">Tú hevur</div>
            <div class="betri-heating-types">
              <div v-bind:key="item" v-for="item in heatingTypes" class="betri-input-radio"><label
                  class="betri-input-label"><input type="radio" :value=item v-model="renovation.HeatingType"
                    @change="onHeatingTypeChanged">{{item}}</label></div>
            </div>
          </div>

          <div class="clearfix spacer-20">
            <div class="burdardygd-column-left">
              <div class="burdardygd-label-and-input" style="margin-top:5px;"><label class="burdardygd-label-title burdardygd-text-bold" for="OilBoilerConstant">Aldur á oljufýri</label>
              </div>
            </div>
            <div class="burdardygd-column-right">
              <select name="OilBoilerIndex" v-model="renovation.OilBoilerIndex" @change="onHeatingTypeChanged"
                style="padding: 0px 0px 0px 15px;">
                <option value="0">{{oilBoilerOptions[0].text}}</option>
                <option value="1">{{oilBoilerOptions[1].text}}</option>
                <option value="2">{{oilBoilerOptions[2].text}}</option>
                <option value="3">{{oilBoilerOptions[3].text}}</option>
              </select>
            </div>
          </div>

          <!-- Oljunýtsla um árið - Slider -->
          <div class="clearfix slider-spacer">
            <div class="burdardygd-column-left margin-bottom">
              <div class="burdardygd-label-and-input"><label class="burdardygd-label-title burdardygd-text-bold" for="oilAmountPrYear">Oljunýtsla um árið</label>
              </div>
            </div>
            <div class="burdardygd-column-right">
              <slider @change="onHeatingTypeChanged" @update="onHeatingTypeChanged" name="oilAmountPrYear" v-model="renovation.oilAmountPrYear" :format="sliderLiterFormat" :min="renovation.minOilAmountPrYear" :max="renovation.maxOilAmountPrYear" :step="100"></slider>
              <div class="betri-calc-row" style="margin-top: 8px;"><span class="betri-currency">{{ this.formatPrice(renovation.minOilAmountPrYear, 0) }} litrar</span>
                <div class=""><span class="betri-currency">{{ this.formatPrice(renovation.maxOilAmountPrYear, 0) }} litrar</span></div>
              </div>
            </div>
          </div>
          
          <div class="burdardygd-label-and-input invisible"><label class="burdardygd-label-title" for="oilPricePrLiter">Verandi
              oljuprísur per litur</label>
            <money3 name="oilPricePrLiter" v-model="renovation.oilPricePrLiter" v-bind="moneyDec">
            </money3>
          </div>
          <div class="burdardygd-label-and-input invisible"><label class="burdardygd-label-title" for="pricePrKwh">Verandi
              elprísur per kWh <sup>*</sup></label>
            <money3 name="pricePrKwh" v-model="renovation.pricePrKwh" v-bind="moneyDec">
            </money3>
          </div>

          <!-- Íløgan kostar - Slider -->
          <div class="clearfix slider-spacer">
            <div class="burdardygd-column-left margin-bottom">
              <div class="burdardygd-label-and-input"><label class="burdardygd-label-title burdardygd-text-bold" for="investmentAmount">Íløgan kostar</label>
              </div>
            </div>
            <div class="burdardygd-column-right">
              <slider name="investmentAmount" v-model="renovation.investmentAmount" :format="sliderCurrencyFormat" :min="renovation.minInvestmentAmount" :max="renovation.maxInvestmentAmount" :step="5000"></slider>
              <div class="betri-calc-row" style="margin-top: 8px;"><span class="betri-currency">{{ this.formatPrice(renovation.minInvestmentAmount, 0) }} kr.</span>
                <div class=""><span class="betri-currency">{{ this.formatPrice(renovation.maxInvestmentAmount, 0) }} kr.</span></div>
              </div>
            </div>
          </div>

          <div class="clearfix spacer-20"></div>

        </div>

        <!-- Bjálving -->
        <div class="neos-col--lion-100" v-show="isBjalving">
          <div style="margin-bottom: 10px;">Tú hevur</div>
          <div class="betri-heating-types">
            <div v-bind:key="item" v-for="item in heatingSystems" class="betri-input-radio"><label
                class="betri-input-label"><input type="radio" :value=item v-model="renovation.HeatingSystem"
                  @change="onHeatingSystemChanged">{{item}}</label></div>
          </div>
          <div v-show="isOilHeatingSystem">
            <div class="burdardygd-label-and-input"><label class="burdardygd-label-title" for="oilAmountPrYear">Árlig
                oljunýtsla áðrenn bjálving</label>
              <money3 name="oilAmountPrYear" v-model="renovation.oilAmountPrYear" v-bind="liter">
              </money3>
            </div>
            <div class="burdardygd-label-and-input"><label class="burdardygd-label-title"
                for="oilAmountAfterInvestmentPrYear">Væntandi oljunýtsla eftir bjálving</label>
              <money3 name="oilAmountAfterInvestmentPrYear" v-model="renovation.oilAmountAfterInvestmentPrYear"
                v-bind="liter">
              </money3>
            </div>
            <div class="burdardygd-label-and-input"><label class="burdardygd-label-title" for="oilPricePrLiter">Verandi
                oljuprísur per litur</label>
              <money3 name="oilPricePrLiter" v-model="renovation.oilPricePrLiter" v-bind="moneyDec">
              </money3>
            </div>
          </div>
          <div v-show="!isOilHeatingSystem">
            <div class="burdardygd-label-and-input"><label class="burdardygd-label-title" for="oilAmountPrYear">Árlig
                elnýtsla áðrenn bjálving</label>
              <money3 name="oilAmountPrYear" v-model="renovation.oilAmountPrYear" v-bind="kwh">
              </money3>
            </div>
            <div class="burdardygd-label-and-input"><label class="burdardygd-label-title"
                for="oilAmountAfterInvestmentPrYear">Væntandi elnýtsla eftir bjálving</label>
              <money3 name="oilAmountAfterInvestmentPrYear" v-model="renovation.oilAmountAfterInvestmentPrYear"
                v-bind="kwh">
              </money3>
            </div>
            <div class="burdardygd-label-and-input"><label class="burdardygd-label-title" for="oilPricePrLiter">Verandi
                elprísur per kWh</label>
              <money3 name="oilPricePrLiter" v-model="renovation.oilPricePrLiter" v-bind="moneyDec">
              </money3>
            </div>
          </div>
          <div class="burdardygd-label-and-input"><label class="burdardygd-label-title" for="investmentAmount">Íløgan kostar</label>
            <money3 name="investmentAmount" v-model="renovation.investmentAmount" v-bind="money">
            </money3>
          </div>
          <div class="burdardygd-label-and-input"><label class="burdardygd-label-title" for="loanAmount">Tín lánstørvur</label>
            <money3 name="loanAmount" v-model="renovation.loanAmount" v-bind="money">
            </money3>
          </div>

          <small class="betri-footer-text-small">
            Útrokningin er grundað á eitt Orkulán við rentustuðli.
            <!-- <br />
            <sup>*</sup>Rentustuðul er íroknaður -->
          </small>
        </div>

        <!-- Húsarhaldstól -->
        <div class="neos-col--lion-100" v-show="isHusarhaldstol">
          <div class="burdardygd-label-and-input"><label class="burdardygd-label-title"
              for="electricityKwhUsagePrYear">Árlig elnýtsla við verandi tóli</label>
            <money3 name="electricityKwhUsagePrYear" v-model="renovation.electricityKwhUsagePrYear" v-bind="kwh">
            </money3>
          </div>
          <div class="burdardygd-label-and-input"><label class="burdardygd-label-title" for="pricePrKwh">Verandi
              prísur per kWh</label>
            <money3 name="pricePrKwh" v-model="renovation.pricePrKwh" v-bind="moneyDec">
            </money3>
          </div>
          <div class="burdardygd-label-and-input"><label class="burdardygd-label-title"
              for="electricityAfterInvestmentKwhUsagePrYear">Væntandi árlig elnýtsla við nýggjum tóli</label>
            <money3 name="electricityAfterInvestmentKwhUsagePrYear"
              v-model="renovation.electricityAfterInvestmentKwhUsagePrYear" v-bind="kwh">
            </money3>
          </div>
          <div class="burdardygd-label-and-input"><label class="burdardygd-label-title" for="investmentAmount">Íløgan kostar</label>
            <money3 name="investmentAmount" v-model="renovation.investmentAmount" v-bind="money">
            </money3>
          </div>
          <div class="burdardygd-label-and-input"><label class="burdardygd-label-title" for="loanAmount">Tín lánstørvur</label>
            <money3 name="loanAmount" v-model="renovation.loanAmount" v-bind="money">
            </money3>
          </div>
        </div>

        <!-- Sólorka -->
        <div class="neos-col--lion-100" v-show="isSolorka">
          <div class="burdardygd-label-and-input"><label class="burdardygd-label-title"
              for="electricityKwhProductionPrYear">Árlig framleiðsla</label>
            <money3 name="electricityKwhProductionPrYear" v-model="renovation.electricityKwhProductionPrYear"
              v-bind="kwh"></money3>
          </div>
          <div class="burdardygd-label-and-input"><label class="burdardygd-label-title"
              for="electricityKwhOwnUsagePrYear">Eginnýtsla</label>
            <money3 name="electricityKwhOwnUsagePrYear" v-model="renovation.electricityKwhOwnUsagePrYear" v-bind="kwh">
            </money3>
          </div>
          <div class="burdardygd-label-and-input"><label class="burdardygd-label-title"
              for="electricityUsageCost">Kostnaður fyri eginnýtslu</label>
            <money3 name="electricityUsageCost" v-model.lazy="renovation.electricityUsageCost" v-bind="moneyDec">
            </money3>
          </div>
          <div class="burdardygd-label-and-input"><label class="burdardygd-label-title"
              for="electricityKwhSoldPrYear">Søla til SEV</label>
            <money3 name="electricityKwhSoldPrYear" v-model.lazy="electricityKwhSoldPrYear" readonly="true"
              v-bind="kwh"></money3>
          </div>
          <div class="burdardygd-label-and-input"><label class="burdardygd-label-title"
              for="electricitySellingPrice">Prísur fyri sølu per kWh</label>
            <money3 name="electricitySellingPrice" v-model="renovation.electricitySellingPrice" v-bind="moneyDec">
            </money3>
          </div>
          <div class="burdardygd-label-and-input"><label class="burdardygd-label-title" for="investmentAmount">Íløgan kostar</label>
            <money3 name="investmentAmount" v-model="renovation.investmentAmount" v-bind="money">
            </money3>
          </div>
          <div class="burdardygd-label-and-input"><label class="burdardygd-label-title" for="loanAmount">Tín lánstørvur</label>
            <money3 name="loanAmount" v-model="renovation.loanAmount" v-bind="money">
            </money3>
          </div>

          <small class="betri-footer-text-small">
            Útrokningin er grundað á eitt Orkulán við rentustuðli.
          </small>
        </div>
      </div>
    </div>

    <!-- output -->
    <div class="betri-grid-column">
      <div class="betri-card">

        <div class="betri-loan-output">

          <div class="burdardygd-column-left-35">

            <div>
              <div class="font-size-120-pct burdardygd-text-bold">Sparing um árið</div>
              <div class="betri-loan-result-top-amount"><span class="betri-currency font-size-120-pct">{{ savingPrYearFormatted }},-</span></div>
            </div>

          </div>

          <div class="burdardygd-column-right-65">
            
            <!-- Jarðhiti og Hitapumpa-->
            <div class="burdardygd-summary" v-show="isJardhiti || isHitapumpa">

              <b>Útrokningin er grundað á:</b>
              <div class="burdardygd-summary-row" style="margin-top:5px;">
                <div class="burdardygd-text-left">Árlig útreiðsla við oljufýri</div>
                <div class="burdardygd-text-right">{{ totalOilPricePrYearFormatted }} kr.</div>
              </div>
              <div class="burdardygd-summary-row line">
                <div class="burdardygd-text-left">Árlig útreiðsla við {{ renovationTypeBending }}</div>
                <div class="burdardygd-text-right">{{ totalElectricityPricePrYearFormatted }} kr.</div>
              </div>
              <div class="burdardygd-summary-row line">
                <div class="burdardygd-text-left">Sparing um mðr.</div>
                <div class="burdardygd-text-right">{{ savingPrMonthFormatted }} kr.</div>
              </div>
              <div class="burdardygd-summary-row line">
                <div class="burdardygd-text-left">
                  Íløgan er spard inn aftur eftir
                </div>
                <div class="burdardygd-text-right">{{ this.formatPrice(repaymentYears, 1) }} ár</div>
              </div>
            </div>

            <!-- Bjálving -->
            <div class="burdardygd-summary" v-show="isBjalving">
              <div class="burdardygd-summary-row">
                <div class="burdardygd-text-left">Sparing um árið</div>
                <div class="burdardygd-text-right burdardygd-text-bold">{{ savingPrYearFormatted }} kr.</div>
              </div>
              <div class="burdardygd-summary-row">
                <div class="burdardygd-text-left">Afturgjaldstíð</div>
                <div class="burdardygd-text-right">{{ repaymentYears }} ár</div>
              </div>
            </div>

            <!-- Sólorka -->
            <div class="burdardygd-summary" v-show="isSolorka">
              <div class="burdardygd-summary-row">
                <div class="burdardygd-text-left">Avkast um mánaðin</div>
                <div class="burdardygd-text-right burdardygd-text-bold">{{ savingPrMonthFormatted }} kr.</div>
              </div>
              <div class="burdardygd-summary-row">
                <div class="burdardygd-text-left">Afturgjaldstíð</div>
                <div class="burdardygd-text-right">{{ repaymentYears }} ár</div>
              </div>
            </div>

            <!-- Húsarhaldstól -->
            <div class="burdardygd-summary" v-show="isHusarhaldstol">
              <div class="burdardygd-summary-row">
                <div class="burdardygd-text-left">Árlig útreiðsla við verandi tóli</div>
                <div class="burdardygd-text-right">{{ totalElectricityPricePrYearFormatted }} kr.</div>
                <!-- <div class="burdardygd-summary-row-tooltip">(keypsprísur ÷ eginfígging)</div> -->
              </div>
              <div class="burdardygd-summary-row">
                <div class="burdardygd-text-left">Árlig útreiðsla við nýggjum tóli</div>
                <div class="burdardygd-text-right">{{ totalElectricityPriceAfterInvestmentPrYearFormatted }} kr.</div>
              </div>
              <div class="burdardygd-summary-row line">
                <div class="burdardygd-text-left">Sparing um árið</div>
                <div class="burdardygd-text-right burdardygd-text-bold">{{ savingPrYearFormatted }} kr.</div>
              </div>
              <div class="burdardygd-summary-row">
                <div class="burdardygd-text-left">Afturgjaldstíð</div>
                <div class="burdardygd-text-right">{{ repaymentYears }} ár</div>
              </div>
            </div>


          </div>

          <div style="clear: both;"></div>

          <a :href="lanLink" target="_blank" class="btn btn-default btn-primary" style="margin-top: -17px;">Rokna títt orkulán</a>
          
          <!-- Jarðhiti og Hitapumpa-->
          <div class="betri-loan-result-bottom" v-show="isJardhiti || isHitapumpa">
            <small class="betri-footer-text-small invisible">Mánaðargjaldið er tann upphædin tú sparir við at gera hesa burðardyggu umvæling.</small>
          </div>
          <!-- Bjálving -->
          <div class="betri-loan-result-bottom" v-show="isBjalving">
            <small class="betri-footer-text-small">Mánaðargjaldið er tann upphædin tú sparir við at gera hesa burðardyggu umvæling.</small>
            <br />
            <small class="betri-footer-text-small">Gev gætur, at tað í flestu førum loysir seg betur fíggjarliga at
              skifta
              til varandi orkuskipan, í staðin
              fyri at bjálva bústaðin.</small>
          </div>
          <!-- Sólorka -->
          <div class="betri-loan-result-bottom" v-show="isSolorka">
            <small class="betri-footer-text-small">Mánaðargjaldið er tann upphædin tú sparir við at gera hesa burðardyggu umvæling.</small>
          </div>

          <!-- Húsarhaldstól -->
          <div class="betri-loan-result-bottom" v-show="isHusarhaldstol">

            <small class="betri-footer-text-small">Mánaðargjaldið er tann upphædin tú sparir við at gera hesa burðardyggu umvæling.</small>

          </div>
        </div>
      </div>
    </div>


    <!-- footer -->
    <div class="betri-grid-column">      
      <div class="betri-footer-text"><small class="betri-footer-text-small"><span>Fyrivarni: </span> 
        Útrokningin er vegleiðindi og grundað á ein oljuprís á {{ this.formatPrice(renovation.oilPricePrLiter) }} krónur/litur, einum elprísi á {{ this.formatPrice(renovation.pricePrKwh) }} krónur/kWh og einum COP á {{ heatingTypeKwhFormatted }}.
        <br />
        COP er sambandið millum hitan, sum {{ renovationTypeBending2 }} framleiðir, og mongdina av streymi hon brúkar.
        <br />
        Við {{ renovationTypeBending }} og {{ heatingTypeBending }} verður væntandi árliga elnýtslan {{ renovation.electricityKwhUsagePrYearFormatted() }} kWh um árið.</small></div>
    </div>
  </div>


</template>

<script>
// Create our number formatter.
const formatter = new Intl.NumberFormat('fo-FO', {
  //style: 'currency',
  // currency: 'DKK',
  // These options are needed to round to whole numbers if that's what you want.
  //minimumFractionDigits: 0, // (this suffices for whole numbers, but will print 2500.10 as $2,500.1)
  //maximumFractionDigits: 0, // (causes 2500.99 to be printed as $2,501)
});

class Renovation {
  OilBoilerOptions = [
    { "value": 0.7, "text": "Eldri enn 1980" },
    { "value": 0.8, "text": "1980-1989" },
    { "value": 0.85, "text": "1990-1999" },
    { "value": 0.9, "text": "2000 og fram" }
  ];
  RenovationTypes = [
    "Jarðhita",
    "Hitapumpu (luft til vatn)",
    //"Sólorku", // <- disabled for now
    //"Bjálving", // <- disabled for now
    //"Húsarhaldstól", // <- disabled for now
  ];
  HeatingTypes = ["Radiator", "Gólvhita"];
  HeatingSystems = ["Oljufýr", "Jarðhita/Hitapumpu"];

  RenovationType = "";
  HeatingType = "";   // radiator or gólvhita
  HeatingSystem = ""; // Slag av orkukeldu fyri bjálving

  minOilAmountPrYear = 1_000;
  maxOilAmountPrYear = 8_000;
  oilAmountPrYear = 3000;
  oilPricePrLiter = 12.75;
  electricityKwhUsagePrYear = 0;
  pricePrKwh = 1.45;
  minLoanAmount = 0;
  maxLoanAmount = 400_000;
  loanAmount = 0;
  minInvestmentAmount = 0;
  maxInvestmentAmount = 400_000;
  investmentAmount = 0;
  loanInterestRate = 1.14;
  rentustudul;// 0.35; // 35% rentustudul (english word is?)

  config = {
    jardhitaCOP: {
      radiator: 4.39,
      floor: 5.8,
    },
    hitapumpaCOP: {
      radiator: 3.19,
      floor: 4.39,
    },
    minOilAmountPrYear: 1_000,
    maxOilAmountPrYear: 8_000,
    oilAmountPrYear: 3000,
    oilPricePrLiter: 12.75,
    electricityKwhUsagePrYear: 0,
    pricePrKwh: 1.45,
    minLoanAmount: 0,
    maxLoanAmount: 400_000,
    loanAmount: 0,
    minInvestmentAmount: 0,
    maxInvestmentAmount: 400_000,
    investmentAmount: 0,
    loanInterestRate: 1.14,
    rentustudul: 0.35, // 35% rentustudul (english word is?)
  };

  /* Case 1: Jarðhiti og Hitapumpa
  Aldur á oljufýri	
  Eldri enn 1980	0,70
  1980-1989	0,80
  1990-1999	0,85
  2000 og fram	0,90 */
  OilBoilerIndex = 1;
  OilBoilerConstant = this.OilBoilerOptions[this.OilBoilerIndex].value;
  OilBoilerText = this.OilBoilerOptions[this.OilBoilerIndex].text;
  //RadiatorHeatingCOPConstant = 4.39; // Radiator
  //FloorHeatingCOPConstant = 5.8; // Gólvhiti

  constructor(renovationType, heatingType) {
    this.RenovationType = renovationType;
    this.HeatingType = heatingType;
  }

  init(config) {
    this.config = config;

    this.minOilAmountPrYear = this.config.minOilAmountPrYear;
    this.maxOilAmountPrYear = this.config.maxOilAmountPrYear;
    this.oilAmountPrYear = this.config.oilAmountPrYear;
    this.oilPricePrLiter = this.config.oilPricePrLiter;
    this.electricityKwhUsagePrYear = this.config.electricityKwhUsagePrYear;
    this.pricePrKwh = this.config.pricePrKwh;
    this.minLoanAmount = this.config.minLoanAmount;
    this.maxLoanAmount = this.config.maxLoanAmount;
    this.loanAmount = this.config.loanAmount;
    this.minInvestmentAmount = this.config.minInvestmentAmount;
    this.maxInvestmentAmount = this.config.maxInvestmentAmount;
    this.investmentAmount = this.config.investmentAmount;
    this.loanInterestRate = this.config.loanInterestRate;
    this.rentustudul = config.rentustudul;

    if (this.checkRenovationType(0)) {
      this.RadiatorHeatingCOPConstant = this.config.jardhitaCOP.radiator;
      this.FloorHeatingCOPConstant = this.config.jardhitaCOP.floor;  
    }
    if (this.checkRenovationType(1)) {
      this.RadiatorHeatingCOPConstant = this.config.hitapumpaCOP.radiator;
      this.FloorHeatingCOPConstant = this.config.hitapumpaCOP.floor;
    }
    // console.log(this.config);
  }

  
  lanLink() {
    return 'https://www.betri.fo/banki/laan/bustadarlan?loanValue=' + this.investmentAmount + '&termValue=' + Math.round(this.repaymentYears()) + '#Roknarin';
  }

  toString() {
    var str = 'Slag av umvæling: '.padEnd(50) + this.RenovationType;

    if (this.RenovationType == this.RenovationTypes[0] || this.RenovationType == this.RenovationTypes[1]) {
      str += '\nSlag av hita: '.padEnd(50) + this.HeatingType;
    }
      str +=
       '\n----------------------------------------'
      + '\nOljunýtsla (litrar) um árið: '.padEnd(50) + this.oilAmountPrYear
      + '\nOljuprísur per litur: '.padEnd(50) + this.oilPricePrLiter
      + '\nEl nýtsla (kWh) um árið: '.padEnd(50) + this.electricityKwhUsagePrYear
      + '\nPrísur per kWh: '.padEnd(50) + this.pricePrKwh
      + '\nStødd á íløgu: '.padEnd(50) + this.loanAmount
      + '\nRentan á láninum: '.padEnd(50) + this.loanInterestRate
      + '\n----------------------------------------'
      + '\nÁrlig útreiðsla við oljufýri: '.padEnd(50) + this.totalOilPricePrYearFormatted()
      + '\nÁrlig útreiðsla við ' + this.RenovationType.toLowerCase() + ': '.padEnd(50) + this.totalElectricityPricePrYearFormatted()
      + '\nSparing um árið: '.padEnd(50) + this.savingPrYearFormatted()
      + '\nAfturgjaldstíð: '.padEnd(50) + this.repaymentYears()
      + '\nSparing um mðr: '.padEnd(50) + this.savingPrMonthFormatted();

    return str;
  }

  changeBoiler() {
    this.OilBoilerConstant = this.OilBoilerOptions[this.OilBoilerIndex].value;
    this.OilBoilerText = this.OilBoilerOptions[this.OilBoilerIndex].text;
  }

  formatPrice(value, digits = 2) {
    let val = (value / 1).toFixed(digits).replace(".", ",");
    return val.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ".");
  }
  electricityKwhUsagePrYearFormatted() {
    return this.formatPrice(this.electricityKwhUsagePrYear, 0);
  }
  realLoanInterestRate() {
    let interest = (this.loanInterestRate - (this.loanInterestRate * this.rentustudul)).toFixed(2);
    return interest;
  }
  nperYears() {
    let amount = this.investmentAmount > 0 ? this.investmentAmount : this.investmentAmount;
    let rate = this.realLoanInterestRate() / 100 / 12;
    let nper = NPER(rate, -this.savingPrMonth(), amount) / 12;
    let years = nper.toFixed(1);

    if(years > 100 || years == 'NaN') {
      return '> 100';
    }
    return years;
  }
  repaymentYears() {

    // let amount = this.loanAmount > 0 ? this.loanAmount : this.investmentAmount;
    // let rate = this.realLoanInterestRate() / 100 / 12;
    // // console.warn('rate: ' + rate);
    // // console.warn('amount: ' + amount);
    // let nper = NPER(rate, -this.savingPrMonth(), amount) / 12;
    // // let years = nper.toFixed(1);

    // // if(years > 100 || years == 'NaN') {
    // //   return '> 100';
    // // }
    // console.log('nper: ' + nper);

    
    //var years = this.investmentAmount / (this.savingPrYear() * (1 - (1 / Math.pow((1 + this.realLoanInterestRate()), this.repaymentYears()))));
    var years = (this.investmentAmount / this.savingPrYear()).toFixed(1);
    return years;
  }
  savingPrMonth() {
    return (this.savingPrYear() / 12);
  }
  savingPrMonthFormatted() {
    return this.formatPrice(this.savingPrMonth(), 0);
  }
  savingPrYear() {
    return this.totalOilPricePrYear() - this.totalElectricityPricePrYear();
  }
  savingPrYearFormatted() {
    return this.formatPrice(this.savingPrYear(), 0);
  }
  totalOilPricePrYearFormatted() {
    return this.formatPrice(this.totalOilPricePrYear(), 0);
  }
  totalElectricityPricePrYearFormatted() {
    return this.formatPrice(this.totalElectricityPricePrYear(), 0);
  }
  totalOilPricePrYear() {
    let result = this.oilAmountPrYear * this.oilPricePrLiter;
    return result;
  }
  totalElectricityPricePrYear() {
    return this.electricityKwhUsagePrYear * this.pricePrKwh;
  }

  //#region Bjálving (specific)
  isOilHeatingSystem() {
    return false;
  }
  //#endregion

  //#region Húsarhaldstól (specific)
  totalElectricityPriceAfterInvestmentPrYear() {
    return 0;
  }
  totalElectricityPriceAfterInvestmentPrYearFormatted() {
    return 0;
  }
  //#endregion

  // Sólorka (specific)
  electricityKwhSoldPrYear() {
    return 0;
  }
  calculateElectricityUsagePrYear() {
    this.electricityKwhUsagePrYear = Math.round((this.oilAmountPrYear * 10 * this.OilBoilerConstant) / this.heatingTypeKwh());
  }
  checkRenovationType(i) {
    return this.RenovationType == this.RenovationTypes[i];
  }
  // computed
  heatingTypeKwh() {
    return this.HeatingType == "Radiator" ? this.RadiatorHeatingCOPConstant : this.FloorHeatingCOPConstant;
  }
  heatingTypeKwhFormatted() {
    return this.formatPrice(this.heatingTypeKwh(), 2);
  }
  heatingType() {
    return this.HeatingType;
  }
}

//#region Concrete renovation class implementations

class JardhitaRenovation extends Renovation {
  constructor() {
    super("Jarðhita", "Radiator");
    this.oilAmountPrYear = 3000;
    this.OilBoilerConstant = 0.8;
    this.loanAmount = 190_000;
    this.investmentAmount = 200_000;
    this.loanInterestRate = 2.15; //1.14;
  }

  lanLink() {
    return 'https://www.betri.fo/banki/laan/bustadarlan/byggja-ella-umvaela?loanValue=' + this.investmentAmount + '&termValue=' + Math.round(this.repaymentYears()) + '#Roknarin';
  }

  toString() {
    let str = super.toString();
    return str;
  }
}

class HitapumpaRenovation extends Renovation {
  constructor() {
    super("Hitapumpu (luft til vatn)", "Radiator");
    this.oilAmountPrYear = 3000;
    this.OilBoilerConstant = 0.8;
    this.loanAmount = 90_000;
    this.investmentAmount = 100_000;
    this.loanInterestRate = 2.15; // 1.75;
  }

  lanLink() {
    return 'https://www.betri.fo/banki/laan/bustadarlan/byggja-ella-umvaela?loanValue=' + this.investmentAmount + '&termValue=' + Math.round(this.repaymentYears()) + '#Roknarin';
  }

  toString() {
    let str = super.toString();
    return str;
  }
}

class BjalvingRenovation extends Renovation {
  oilAmountAfterInvestmentPrYear = 900;

  // variables that are not used: electricityKwhUsagePrYear, pricePrKwh

  constructor() {
    super("Bjálving", "");
    this.HeatingSystem = this.HeatingSystems[0];
    this.oilAmountPrYear = 3000;
    this.oilAmountAfterInvestmentPrYear = 2000;
    this.oilPricePrLiter = 12.75//10.5;
    this.loanAmount = 20_000;
    this.investmentAmount = 30_000;
    this.loanInterestRate = 2.15;
  }

  toString() {
    var str = 'Slag av umvæling: '.padEnd(50) + this.RenovationType;

    if (this.isOilHeatingSystem()) {
      str += '\nÁrlig oljunýtsla áðrenn bjálving: '.padEnd(50) + this.oilAmountPrYear
        + '\nVæntandi oljunýtsla eftir bjálving: '.padEnd(50) + this.oilAmountAfterInvestmentPrYear
        + '\nVerandi oljuprísur per litur: '.padEnd(50) + this.oilPricePrLiter;
    }
    else {
      str += '\nÁrlig elnýtsla áðrenn bjálving: '.padEnd(50) + this.oilAmountPrYear
        + '\nVæntandi elnýtsla eftir bjálving: '.padEnd(50) + this.oilAmountAfterInvestmentPrYear
        + '\nVerandi elprísur per kWh: '.padEnd(50) + this.oilPricePrLiter;
    }

    str += '\nHvussu stór er íløgan? '.padEnd(50) + this.loanAmount
      + '\nHvat er rentan á láninum? '.padEnd(50) + this.loanInterestRate
      + '\n----------------------------------------'
      + '\nSparing um árið: '.padEnd(50) + this.savingPrYearFormatted()
      + '\nAfturgjaldstíð: '.padEnd(50) + this.repaymentYears()
      + '\nSparing um mðr: '.padEnd(50) + this.savingPrMonthFormatted();
    
    return str;
    // let str = super.toString()
    //   + '\nSparing um árið: '.padEnd(50) + this.savingPrYearFormatted();
    // return str;
  }

  // override base function
  savingPrYear() {
    return (this.oilAmountPrYear - this.oilAmountAfterInvestmentPrYear) * this.oilPricePrLiter;
  }

  isOilHeatingSystem() {
    return this.HeatingSystem == this.HeatingSystems[0];
  }

  changeHeatingSystemValues() {
    if (this.isOilHeatingSystem()) {
      this.oilAmountPrYear = 3000;
      this.oilAmountAfterInvestmentPrYear = 2000;
      this.oilPricePrLiter = 8.22;
      this.loanAmount = 20_000;
      this.investmentAmount = 30_000;
    }
    else {
      this.oilAmountPrYear = 5000;
      this.oilAmountAfterInvestmentPrYear = 4000;
      this.oilPricePrLiter = 1.45;
      this.loanAmount = 90_000;
      this.investmentAmount = 100_000;
    }
  }
}

class HusarhaldstolRenovation extends Renovation {
  electricityAfterInvestmentKwhUsagePrYear = 300;

  constructor() {
    super("Húsarhaldstól", "");
    this.electricityKwhUsagePrYear = 2000;
    this.pricePrKwh = 1.95;
    this.loanAmount = 2000;
    this.investmentAmount = 3000;
    this.loanInterestRate = 3.25; // 2.5;
    this.rentustudul = 0.0;
  }

  toString() {

    var str = 'Slag av umvæling: '.padEnd(50) + this.RenovationType
      + '\nÁrlig elnýtsla við verandi tóli: '.padEnd(50) + this.electricityKwhUsagePrYearFormatted()
      + '\nVerandi prísur per kWh: '.padEnd(50) + this.pricePrKwh
      + '\nVæntandi árlig elnýtsla við nýggjum tóli: '.padEnd(50) + this.electricityAfterInvestmentKwhUsagePrYear
      + '\nHvussu stór er íløgan? '.padEnd(50) + this.loanAmount
      + '\nHvat er rentan á láninum? : '.padEnd(50) + this.loanInterestRate
      + '\n----------------------------------------'
      + '\nÁrlig útreiðsla við verandi tóli: '.padEnd(50) + this.totalElectricityPricePrYearFormatted()
      + '\nÁrlig útreiðsla við nýggjum tóli: '.padEnd(50) + this.totalElectricityPriceAfterInvestmentPrYearFormatted()
      + '\nSparing um árið: '.padEnd(50) + this.savingPrYearFormatted()
      + '\nAfturgjaldstíð: '.padEnd(50) + this.repaymentYears()
      + '\nSparing um mðr: '.padEnd(50) + this.savingPrMonthFormatted();
    return str;
  }

  // override base function, because it should not be calculated
  calculateElectricityUsagePrYear() {
    // do nothing
  }

  // override base function
  savingPrYear() {
    return this.totalElectricityPricePrYear() - this.totalElectricityPriceAfterInvestmentPrYear();
  }

  totalElectricityPriceAfterInvestmentPrYear() {
    return this.electricityAfterInvestmentKwhUsagePrYear * this.pricePrKwh;
  }

  totalElectricityPriceAfterInvestmentPrYearFormatted() {
    return this.formatPrice(this.totalElectricityPriceAfterInvestmentPrYear(), 0);
  }

}

class SolorkaRenovation extends Renovation {
  electricityKwhProductionPrYear; // Árlig framleiðsla (kWh)
  electricityKwhOwnUsagePrYear; // Árlig eginnýtsla (kWh)
  electricityUsageCost; // Kostnaður fyri eginnýtslu (kr)
  electricitySellingPrice; // Prísur fyri sølu per kWh (kr)
  //electricityProductionPrice; // Árlig framleiðsla per kWh (kr)

  constructor() {
    super("Sólorku", "");
    this.electricityKwhProductionPrYear = 7_000;
    this.electricityKwhOwnUsagePrYear = 2_000;
    this.electricityUsageCost = 1.95;
    this.electricitySellingPrice = 0.93;
    //this.electricityProductionPrice = 1.57;

    this.loanAmount = 100_000;
    this.investmentAmount = 125_000;
    this.loanInterestRate = 2.15;// 0.01;
  }

  lanLink() {
    return 'https://www.betri.fo/banki/laan/bustadarlan/byggja-ella-umvaela?loanValue=' + this.investmentAmount + '&termValue=' + Math.round(this.repaymentYears()) + '#Roknarin';
  }

  toString() {
    var str = 'Slag av umvæling: '.padEnd(50) + this.RenovationType
      + '\nHvussu stór er íløgan?: '.padEnd(50) + this.loanAmount
      + '\nÁrlig framleiðsla: '.padEnd(50) + this.electricityKwhProductionPrYear
      + '\nEginnýtsla: '.padEnd(50) + this.electricityKwhOwnUsagePrYear
      + '\nKostnaður fyri eginnýtslu: '.padEnd(50) + this.electricityUsageCost
      + '\nSøla til SEV: '.padEnd(50) + this.electricityKwhSoldPrYear()
      + '\nPrísur fyri sølu per kWh: '.padEnd(50) + this.electricitySellingPrice
      + '\n----------------------------------------'
      + '\nAvkast um mánaðin: '.padEnd(50) + this.savingPrMonthFormatted()
      + '\nAfturgjaldstíð: '.padEnd(50) + this.repaymentYears()
      + '\nSparing um mðr: '.padEnd(50) + this.savingPrMonthFormatted();
    return str;
  }

  // Søla til SEV (kWh)
  electricityKwhSoldPrYear() {
    return this.electricityKwhProductionPrYear - this.electricityKwhOwnUsagePrYear;
  }

  productionTotalPrYear() {
    return this.ownUsageTotalPrYear() + this.soldTotalPrYear();
  }

  ownUsageTotalPrYear() {
    return this.electricityKwhOwnUsagePrYear * this.electricityUsageCost;
  }

  soldTotalPrYear() {
    var diff = this.electricityKwhProductionPrYear - this.electricityKwhOwnUsagePrYear;
    return diff * this.electricitySellingPrice;
  }

  // Árlig framleiðsla per kWh (kr)
  electricityProductionPrice() {
    var result = this.productionTotalPrYear() / this.electricityKwhProductionPrYear;
    return result;
  }

  // this is actually profit pr year
  savingPrYear() {
    return this.productionTotalPrYear();
  }

}

