import slider from '@vueform/slider'
import { NPER } from '@formulajs/formulajs'

export default {
  name: 'App',
  components: {
    slider
  },
  data() {
    return {
      //#region Money and other unit formats
      money: {
        decimal: ',',
        thousands: '.',
        prefix: '',
        suffix: ' kr. ',
        precision: 0,
        masked: false
      },
      moneyDec: {
        decimal: ',',
        thousands: '.',
        prefix: '',
        suffix: ' kr. ',
        precision: 2,
        masked: false
      },
      kwh: {
        decimal: ',',
        thousands: '.',
        prefix: '',
        suffix: ' kWh ',
        precision: 0,
        masked: false
      },
      liter: {
        decimal: ',',
        thousands: '.',
        prefix: '',
        suffix: ' litrar ',
        precision: 0,
        masked: false
      },
      pct: {
        decimal: ',',
        thousands: '.',
        prefix: '',
        suffix: ' % ',
        precision: 2,
        masked: false
      },
      //#endregion
      renovation: new JardhitaRenovation(),
      sliderValue: 20,
      sliderCurrencyFormat: function (value) {
        return formatter.format(value).replace(',', '.') + ' kr.';
      },
      sliderLiterFormat: function (value) {
        return formatter.format(value).replace(',', '.') + ' litrar';
      }
    }
  },
  computed: {
    heatingTypeBending() {
      var res = this.renovation.HeatingType.toLowerCase();
      switch (res) {
        case "radiator":
          res = "radiatori";
          break;
        case "gólvhita":
          res = "gólvhita";
          break;
      }
      return res;
    },
    renovationTypeBending() {
      var res = this.renovation.RenovationType.toLowerCase();
      switch (res)
      {
        case "hitapumpa":
          res = "hitapumpu";
          break;
        case "jarðhita":
          res = "jarðhita";
          break;
       }
      return res;
    },
    renovationTypeBending2() {
      var res = this.renovation.RenovationType.toLowerCase();
      switch (res) {
        case "hitapumpu":
          res = "hitapumpan";
          break;
        case "jarðhita":
          res = "jarðhitaskipanin";
          break;
      }
      return res;
    },
    heatingTypeKwhFormatted() {
      return this.renovation.heatingTypeKwhFormatted();
    },
    oilBoilerOptions() {
      return this.renovation.OilBoilerOptions;
    },
    renovationTypes() {
      return this.renovation.RenovationTypes;
    },
    heatingTypes() {
      return this.renovation.HeatingTypes;
    },
    heatingSystems() {
      return this.renovation.HeatingSystems;
    },
    renovationType() {
      return this.renovation.RenovationType;
    },
    nperYears(){
      return this.renovation.nperYears();
    },
    repaymentYears() {
      return this.renovation.repaymentYears();
    },
    totalOilPricePrYearFormatted() {
      return this.renovation.totalOilPricePrYearFormatted();
    },
    totalElectricityPricePrYearFormatted() {
      return this.renovation.totalElectricityPricePrYearFormatted();
    },

    // Húsarhaldstól (specific)
    totalElectricityPriceAfterInvestmentPrYearFormatted() {
      return this.renovation.totalElectricityPriceAfterInvestmentPrYearFormatted();
    },

    // Sólorka (specific)
    electricityKwhSoldPrYear() {
      return this.renovation.electricityKwhSoldPrYear();
    },

    savingPrYearFormatted() {
      return this.renovation.savingPrYearFormatted();
    },
    savingPrMonthFormatted() {
      return this.renovation.savingPrMonthFormatted();
    },
    isJardhiti() {
      return this.renovation.checkRenovationType(0);
    },
    isHitapumpa() {
      return this.renovation.checkRenovationType(1);
    },
    isSolorka() {
      return this.renovation.checkRenovationType(2);
    },
    isBjalving() {
      return this.renovation.checkRenovationType(3);
    },
    isHusarhaldstol() {
      return this.renovation.checkRenovationType(4);
    },
    isOilHeatingSystem() {
      return this.renovation.isOilHeatingSystem();
    },
    lanLink() {
      return this.renovation.lanLink();
    }
  },
  mounted() {
    this.renovation.init(this.$burdardygdConfig);
    this.renovation.calculateElectricityUsagePrYear();

    // hide MVC Form on sitecore (if it exists)
    var formModuleA = document.getElementsByClassName('form-module-a');
    if(formModuleA.length > 0) {
      formModuleA[0].style.visibility = "hidden";
    }
  },
  methods: {
    formatPrice(value, digits = 2) {
      let val = (value / 1).toFixed(digits).replace(".", ",");
      return val.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ".");
    },
    onRenovationTypeChanged() {
      if (this.isJardhiti) {
        this.renovation = new JardhitaRenovation();
      } else if (this.isHitapumpa) {
        this.renovation = new HitapumpaRenovation();
      } else if (this.isSolorka) {
        this.renovation = new SolorkaRenovation();
      } else if (this.isBjalving) {
        this.renovation = new BjalvingRenovation();
      } else if (this.isHusarhaldstol) {
        this.renovation = new HusarhaldstolRenovation();
      }
      this.renovation.init(this.$burdardygdConfig);
      this.renovation.calculateElectricityUsagePrYear();
    },
    onHeatingTypeChanged() {
      this.renovation.changeBoiler();
      this.renovation.calculateElectricityUsagePrYear();
    },
    onHeatingSystemChanged() {
      this.renovation.changeHeatingSystemValues();
      this.renovation.calculateElectricityUsagePrYear();
    }
  }
}
</script>
